@import url('https://fonts.googleapis.com/css2?family=Karla:wght@300;600;700;800&display=swap');
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: Karla, 'sans-serif';
}
*::before,
*::after{
	box-sizing: border-box;
}
.flex-center{
	display: flex;
	justify-content: center;
	align-items: center;
}
body {
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.App{
	min-width: 700px;
	min-height: 90vh;
	background: #F5F7FB;
	border-radius: 10px;
	overflow: hidden;
	display: flex;
}
.start{
	width: 100%;
	background: 
	url("./Images/blob-up-big.png") no-repeat top right,
	url("./Images/blob-down-big.png") no-repeat bottom left;
	flex-direction: column;
	gap: 20px;
}
.quizz{
	width: 100%;
	background: 
	url("./Images/blob-up-small.png") no-repeat top right,
	url("./Images/blob-down-small.png") no-repeat bottom left;
	flex-direction: column;
	justify-content: space-between;
	gap: 20px;
	padding: 40px 80px;
	font-size: 12px;
}
.btn{
	border: none;
	border-radius: 10px;
	padding: 15px 50px;
	background-color: #4D5B9E;
	color: #fff;
	cursor: pointer;
}
.section{
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 5px;
	width: 100%;
}
.title{
	font-size: 16px;
	font-weight: 600;
	text-align: left;
	width: 100%;
}
.options-container{
	display: flex;
	width: 100%;
	gap: 10px;
}
.option{
	display: inline;
	margin: 10px 0;
	padding: 5px 20px;
	border-radius: 10px;
	border: 1px solid #4D5B9E;
	cursor: pointer;
}
.option:hover{
	background-color: #D6DBF5;
}
.separator{
	width: 100%;
	height: 1px;
	background: #4D5B9E;
	opacity: .3;
}
.bottom-box{
	gap: 40px
}
.option:disabled{
	color:#000;
	cursor:auto;
}
.bottom-box p{
	font-size: 1rem;
	font-weight: 700;
}
.custom-select {
  /* appearance: none; */
  background-color: transparent;
  border: 1px solid #4D5B9E;
	border-radius: 5px;
  padding: 3px 7px;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
	cursor: pointer;
	display: grid;
}
